import React , {Component} from 'react';
import './css.css';
import $ from 'jquery';

import {setLogin, LoginUser} from "./index";

class Login extends Component {
    constructor() {
        super();
        this.state = {word1: '', word2: ''};
        this.handleWord1Change = this.handleWord1Change.bind(this);
        this.handleWord2Change = this.handleWord2Change.bind(this);
    }
    ladder = "";
    handleWord1Change(e) {
        this.setState({word1:e.target.value});
    }
    handleWord2Change(e) {
        this.setState({word2:e.target.value});
    }
    handleConfirmClick = () => {
        $.ajax({
            url:"/find",
            data:{
                username:this.state.word1,
                password:this.state.word2
            },
            context:document.body,
            async:false,
            type:"get",
            success: function(data) {
                this.ladder = data;
            }
        });
        window.location.reload();
    };

    render() {
        let islogin = false;
        $.ajax({
            url:"/checkstate",
            context:document.body,
            async:false,
            type:"get",
            success: function(data) {
                if(data !== "false") {
                    islogin = true;
                }
            }
        });
        if(islogin) {
            setLogin(true, LoginUser);
        }
        else {
            setLogin(false, null);
        }

        return (
            <div className="LoginFun">
                <div>
                    <div>Word1:</div>
                    <input type="text" value={this.state.wrod1} onChange={this.handleWord1Change}/>
                </div>
                <div>
                    <div>Word2:</div>
                    <input type="text" value={this.state.word2} onChange={this.handleWord2Change}/>
                </div>
                <div>
                    <button onClick={this.handleConfirmClick} className="Button1">Confirm</button>
                </div>
                <div>
                    <div>Ladder:</div>
                    <div>{this.ladder}</div>
                </div>
            </div>
        );
    }
}

export default Login;


